import React from 'react';



const  CardList =({robots}) => {
	return ( 
							<div >
								{
									robots.map((item,i) => {
								return (
									<article class="center mw5 mw6-ns br3 hidden ba b--black-10 mv4 box shadow-5">
  <h1 class="f4 bg-near-white br3 br--top black-60 mv0 pv2 ph3">{robots[i].name}</h1>
  <div class="pa3 bt b--black-10">
    <p class="f6 f5-ns lh-copy measure">
     <img
     alt ="Please wait, reaction willbe displayed here"
     src ={robots[i].id}
     />
    </p>
  </div>
</article>
									)
								})
								}
							</div>
		)
}

export default CardList;