import React from 'react';



const Category = ( {rroute}) => {
	return (
		<div>
		<h1>Please select a category </h1>
		<p 
				className="f6 grow no-underline br-pill ba bw2 ph3 pv2 mb2 dib dark-green pointer" 
				onClick ={( ) => rroute("namesearch")}
				>
				Name reactions
				</p>
				<br />
				<p 
				className="f6 grow no-underline br-pill ba bw2 ph3 pv2 mb2 dib dark-green pointer" 
				onClick ={( ) => rroute("namesearch")}
				>
				Chemical Equations
				</p>
				
				</div>
		);

}

export default Category;