import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar.js'
import 'tachyons';
import NameSearch from './components/Search/NameSearch.js';
import Home from './components/Home/Home.js';
import { NameReaction }  from './components/Reaction/NameReaction';
import CardList from './components/Lists/CardList/CardList.js';
import Category from './components/Category/Category.js'
import Scroll from './components/Scroll/Scroll.js'
import Logo from './components/Logo/Logo.js'




class App extends React.Component  {
  constructor ( ) {
    super( );
    this.state ={
      input : " ",
      route : "home",
      searchField : " ",
      robots : [],
    }
  }

  


onSearch = ( event) => {
    this.setState({searchField : event.target.value})
    console.log(this.state.searchField)
}

oninput = (event) => {
   this.setState({ input : event.target.value })
}

routeChange = ( route) => {
            this.setState({route : route})
}

componentDidMount ( ) {
  this.setState({robots : NameReaction})
}


  render( ) {

  const filteredrobots = this.state.robots.filter(robot => {
      return robot.name.toLowerCase( ).includes(this.state.searchField.toLowerCase( ));
        })
            return (
    <div className="App "> 
     
    {this.state.route === "home"
    ?    (<div>
            <Logo />
            <Home  onRoute ={this.routeChange} />
            </div>
          )    
    :    (   
                           this.state.route === "namesearch"
                           ? (
                                    <div>
                                        <Navbar  Route = {this.routeChange} />
                                        <NameSearch onS ={this.onSearch} />
                                        <Scroll >
                                        <CardList robots ={filteredrobots}  />
                                        </Scroll>
                                      </div>
                                )
                            : (
                            <div>
                           <Navbar  Route = {this.routeChange} />
                           <Category  rroute ={this.routeChange} />
                           </div>
                              )

                    
                  )
                  
          
  }
     
        
    </div>
  );
  }
}

export default App;
