import React from 'react'
import './Navbar.css';
import Logo from '../Logo/Logo.js'

const Navbar = ( {Route} ) => {
	return (
		<nav >
		<Logo />
		<p 
			className="f3 link dim black underline pa3 pointer fr"
			 onClick = { ( ) => Route("home")}
			>
			Home
			</p>
			</nav>
		);
}

export default Navbar;