import React from 'react';
  
   

const ImageLink = ( {onsearch}) => {
	return (
		<div className="center">
				<div className="form center pa2 br3 br-pill" >
				<input 
				type = "text" 
				className="f5 pa2 w-70 center br-pill "
				onChange ={onsearch}
				placeholder = "Type the name or the name of first element/compound of the reaction."
				/>
				</div>
  			</div>

		);
}


export default ImageLink;